"use client";

import { motion } from "framer-motion";

export default function Home() {
  return (
    <section className="hero">
      
      <motion.h1
        initial={{ opacity: 0, y: -40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
      >
        Hi, Iâ€™m <span>Mohd Asif Ali</span>
      </motion.h1>

      <motion.h3
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.3 }}
      >
        Software Developer | MERN Stack | Next.js
      </motion.h3>

      <motion.p
        initial={{ opacity: 0, y: 30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.6 }}
        className="hero-text"
      >
        I am a passionate Software Developer with hands-on experience
        in building scalable and responsive web applications.
        I specialize in React, Next.js, Node.js and MongoDB.
        I enjoy solving real-world problems and building user-friendly solutions.
      </motion.p>

      <motion.div
        className="hero-buttons"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.9 }}
      >
        <a href="/projects">View Projects</a>
        <a href="/contact" className="outline-btn">Hire Me</a>
      </motion.div>

    </section>
  );
}
